<template>
    <div>
        <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane label="todolist" name="first">
                todolist
            </el-tab-pane>
            <el-tab-pane label="" name="second">
                <el-row :gutter="20">
                    <el-col :span="12">
                        
                        <el-table :data="tableData" style="width: 100%">
                            <el-table-column prop="date" label="" width="180">
                            </el-table-column>
                            <el-table-column prop="name" label="" width="180">
                            </el-table-column>
                            <el-table-column prop="address" label="">
                            </el-table-column>
                        </el-table>
                    </el-col>
                    <el-col :span="12">
                        
                        <el-table :data="tableData" style="width: 100%">
                            <el-table-column prop="date" label="" width="180">
                            </el-table-column>
                            <el-table-column prop="name" label="" width="180">
                            </el-table-column>
                            <el-table-column prop="address" label="">
                            </el-table-column>
                        </el-table>
                    </el-col>
                </el-row>
                <label>
                    : <input type="number" v-model="expenditure">
                </label>
                <label>
                    : <input type="text" v-model="something">
                </label>
                <button></button>
            </el-tab-pane>
            <el-tab-pane label="" name="third">
                
            </el-tab-pane>
            <el-tab-pane label="" name="fourth">
                
            </el-tab-pane>
        </el-tabs>
    </div>
</template>
<script>
export default {
    data () {
        return {
            activeName: 'second',
            expenditure: 0,
            something: '',
            tableData: [{
                date: '2016-05-02',
                name: '5.5',
                address: ''
            }, {
                date: '2016-05-04',
                name: '14',
                address: ''
            }, {
                date: '2016-05-01',
                name: '5',
                address: ''
            }, {
                date: '2016-05-03',
                name: '6',
                address: ''
            }]
        };
    },
    methods: {
        handleClick (tab, event) {
            console.log(tab, event);
        }
    }
}
</script>
<style scoped>
</style>



